
.wp-site-blocks {
	background: #fff;
	border-top: 2px solid #bbb;
	margin: 2rem auto;
	max-width: 1000px;
}

.site-header {
	padding: 2rem 0 0;

	.alignwide {
		padding: 0 4rem;
	}
}

.site-main {
    max-width: 80%;
    margin: 2rem auto;
}

.site-footer {
    border-top: 1px solid #ddd;
    padding: 2rem 0 0;
    margin-top: 6rem;

	.alignwide {
		padding: 0 4rem;
	}
}

.colophon {
    background: #f9f9f9;
    border-top: 1px solid #ddd;
    padding: 1rem 0;
}


